import { LanguageService } from './../../../../services/language.service';
import { VhComponent } from './../../../../components/vh-component/vh-component';
import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { VhAuth } from 'ionic-vhframeworks';

@Component({
  selector: 'app-discount-employee',
  templateUrl: './discount-employee.component.html',
  styleUrls: ['./discount-employee.component.scss']
})
export class DiscountEmployeeComponent implements OnInit {

  constructor(
    private router:Router,
    private vhAuth: VhAuth,
    private vhComponent: VhComponent,
    private lang: LanguageService

  ) { }

  ngOnInit(): void {
  }

  gotoSalesDiscount(){
    if (this.vhAuth.checkMyPermission('management_enable_sales_discount'))
    this.router.navigate(['/sales/dashboard/manage/sales-program/discount-employee/sales-discount'])
    else this.vhComponent.alertMessageDesktop("error", this.lang.translate("You do not have this rights"), 3000)
  }

  goBack() {
    this.router.navigate(['/sales/dashboard/manage/sales-program'])
  }
}
