
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DiscountEmployeeComponent } from './discount-employee.component';
import { RouterModule, Routes } from '@angular/router'; 
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';

const routes: Routes = [
  {
    path: '',
    component: DiscountEmployeeComponent
  },
  {
    path: 'sales-discount',
    loadChildren: () => import('./sales-discount/sales-discount.module').then(m => m.SalesDiscountModule)
  } 
]


@NgModule({
  declarations: [
    DiscountEmployeeComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule
  ]
})
export class DiscountEmployeeModule { }
